* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header {
    padding: 20px 10px;
    font-weight: bold;
    font-size: 2rem;
    background-color: lightcyan;
    text-align: center;
    border-bottom: 1px solid #eee;
}

body {
    width: 800px;
    margin: auto;
}

.list {
    padding: 20px 10px;
    border-bottom: 1px solid lightgray;
    display: flex;
    flex-wrap: wrap;
    background-color: mintcream;
}

.listname {
    width: calc(100% - 100px);
}

.good {
    width: 100px;
}

.good>span {
    cursor: pointer;
}

.content {
    width: 100%;
    padding-top: 10px;
}

.date {
    width: 100%;
    font-size: 0.75rem;
    padding: 10px 0;
}

.modal {
    background-color: #eee;
    padding: 20px;
    position: absolute;
    top: 100px;
    right: 0;
    width: 50%;
}

.input {
    text-align: center;
    padding: 10px;
    background-color: lightcyan;
    position: relative;
    height: 100px;
}

.text {
    border: 1px solid red;
    color: white;
    padding: 10px;
}

.btn-7 {
    background: #000;
    color: #fff;
    line-height: 30px;
    padding: 0;
    border: none;
    z-index: 1;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
    position: absolute;
    width: 60px;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -70%);
}

.btn-7:hover {
    background: transparent;
    color: #000;
}

.btn-7:before,
.btn-7:after {
    position: absolute;
    content: "";
    left: 0;
    width: 100%;
    height: 50%;
    right: 0;
    z-index: -1;
    background: #000;
    transition: all 0.3s ease;
}

.btn-7:before {
    top: 0;
}

.btn-7:after {
    bottom: 0;
}

.btn-7:hover:before,
.btn-7:hover:after {
    height: 0;
    background-color: #000;
}